@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@600;700&display=swap);
.button-box{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;padding-top:40px}.button-box__button{padding:0;height:8px;width:8px;border:0;border-radius:50%;background-color:rgba(255,255,255,0.4);outline:none}.button-box__button--active{background-color:#fff}.button-box__label{padding:22.5px;margin:0}

.slide__img{margin:auto;margin-bottom:63px}.slide__title{margin-bottom:15px;text-align:center;font-family:"Montserrat", sans-serif;font-size:20px;font-weight:600;color:white}.slide__text{margin-bottom:84px;width:500px;text-align:center;font-family:"Montserrat", sans-serif;font-size:14px;font-weight:600;color:white}

.slider{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;width:56%;height:860px;overflow:hidden;box-sizing:border-box;padding-top:200px;padding-bottom:75px;border-radius:15px;background-image:url(data:image/png;base64,);background-position:center;background-size:cover}.slider__wraper{display:-webkit-flex;display:flex;width:300%;transition:all 0.5s ease 0s}.slider__window{width:500px;margin:auto 0;overflow:hidden}@media screen and (max-width: 1000px){.slider{width:100%}}

.sign-in-input{position:relative}.sign-in-input__label{display:block;font-size:14px;font-weight:600}.sign-in-input__input{width:100%;height:54px;margin-top:14px;margin-bottom:30px;padding-left:20px;border:1px solid #e0e5e9;border-radius:8px;font-weight:600;font-size:14px}.sign-in-input__input--error{border:1px solid #FF4B26}.sign-in-input__input--is-logined{border:1px solid #6FCF97}.sign-in-input__input:focus{border:1px solid #065bea}.sign-in-input__link-password{position:absolute;right:20px;top:52px;text-decoration:none;color:#75777a}

.button{width:100%;height:54px;border-radius:8px;background-color:#065bea;border:0;color:#fff;font-size:16px;transition:background-color 0.2s}.button:hover{background-color:#0451d2}.button:active{background-color:#065bea}.button:disabled{background-color:#c6d0e0}

.sign-in-form{font-weight:600;font-size:14px}.sign-in-form__title{margin-bottom:15px;font-size:28px;font-weight:700}.sign-in-form__sign-up-link{margin-left:9px;text-decoration:none;color:#065bea}.sign-in-form__head{margin-bottom:61px}

.links-box{margin-top:190px}.links-box__link{margin-right:25px;text-decoration:none;color:#75777a;font-size:14px;font-weight:600}

.sign-in{padding:51px 120px 0 82px;width:44%}.sign-in__logo{margin-bottom:112px}@media screen and (max-width: 1000px){.sign-in{width:100%}}

.main{display:-webkit-flex;display:flex;padding:20px}@media screen and (max-width: 1000px){.main{-webkit-flex-direction:column;flex-direction:column;padding:10px}}

*,*::before,*::after{box-sizing:border-box;outline:none}ul[class],ol[class]{padding:0}body,h1,h2,h3,h4,p,ul[class],ol[class],li,figure,figcaption,blockquote,dl,dd{margin:0}body{min-height:100vh;scroll-behavior:smooth;text-rendering:optimizeSpeed;line-height:1.5}ul,ol{list-style:none}a:not([class]){-webkit-text-decoration-skip:ink;text-decoration-skip-ink:auto}img{max-width:100%;display:block}article>*{margin-top:1em}input,button,textarea,select{font:inherit}

*{font-family:"Montserrat", sans-serif;color:#333}


